#header {
    min-width: 100%;
    height: 100%;
    top: 40px;
    margin-top: 60px;
    /* z-index: 25; */
    /* position: fixed; */
}

.item {
    background-color:rgb(0, 0, 0);
    opacity: .8;
    /* z-index: 40; */
    position: relative;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    box-shadow: 0px 0px 8px #f7e27b;
    -moz-box-shadow: 0px 0px 8px #f7e27b;
    -webkit-box-shadow: 0px 0px 8px #f7e27b;

}

.h5 {
    background-color:rgb(0, 0, 0);
    opacity: .8;
    /* z-index: 35; */
    padding-left: 5px;
    padding-right: 5px;
    position: relative;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    box-shadow: 0px 0px 8px #f7e27b;
    -moz-box-shadow: 0px 0px 8px #f7e27b;
    -webkit-box-shadow: 0px 0px 8px #f7e27b;

}

